@import url(https://fonts.googleapis.com/css?family=Nunito&display=swap);
.card{background-image:url(/its-the-gorilla/static/media/background.3d957a82.jpg);background-size:20em;height:10em;width:7em;box-shadow:0 0 1.1em 0.5em rgba(0,0,0,0.5);border-radius:0.75em;display:flex;flex-direction:column;margin:1em;position:relative;font-size:0.9rem;cursor:pointer;transition:transform 0.5s;-webkit-user-select:none;-ms-user-select:none;user-select:none}.card:hover{transform:scale(1.025)}@media only screen and (min-width: 600px){.card{font-size:1.5rem}}.card::after{content:"";position:absolute;height:1.1em;width:1.4em;background-image:url(/its-the-gorilla/static/media/pin.8256ad05.png);top:0.25em;right:0.25em;background-size:cover}.card__main{color:black;display:flex;flex-direction:column;justify-content:center;align-items:center;flex:3 1;position:relative}.card__main::after{position:absolute;content:"";height:100%;width:100%;background-color:rgba(255,255,255,0.6);border-radius:0.75em 0.75em 0 0}.card__description{position:absolute;z-index:100;transform:rotateZ(-3deg);font-size:1.1em;text-align:center;line-height:1em;padding:1em 1em 0}.card__footer{background:black;flex:1 1;border-radius:0 0 0.75em 0.75em;display:flex;align-items:center;justify-content:center}.card__number{color:white;font-size:2em;margin-right:0.25em}

.card-list{display:flex;flex-wrap:wrap;justify-content:space-evenly;-webkit-backdrop-filter:blur(1px);backdrop-filter:blur(1px);padding-top:1.5em}

.app{background-image:url(/its-the-gorilla/static/media/wood.d519e6aa.jpg);background-position:center;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center}

.color-picker{background-color:rgba(255,255,255,0.75);width:100vw;display:flex;justify-content:center;align-items:center;top:0;z-index:1;height:1.5em;position:fixed}.color-picker__selected-color{height:100%;flex:1 1}

*,*::before,*::after{box-sizing:border-box}ul[class],ol[class]{padding:0}body,h1,h2,h3,h4,p,ul[class],ol[class],li,figure,figcaption,blockquote,dl,dd{margin:0}body{min-height:100vh;scroll-behavior:smooth;text-rendering:optimizeSpeed;line-height:1.5}ul[class],ol[class]{list-style:none}a:not([class]){-webkit-text-decoration-skip:ink;text-decoration-skip-ink:auto}img{max-width:100%;display:block}article>*+*{margin-top:1em}input,button,textarea,select{font:inherit}@media (prefers-reduced-motion: reduce){*{animation-duration:0.01ms !important;animation-iteration-count:1 !important;transition-duration:0.01ms !important;scroll-behavior:auto !important}}body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:"Caveat", sans-serif;font-weight:bold;font-size:1.2em}

